/*Global Style's */
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
body {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
/*The buttons */
.btn {
  display: inline-block;
  border: none;
  background: #000;
  color: #fff;
  border-radius: 10px;
  padding: 15px;
}

/*The main Container */
.container {
  width: 95%;
  margin: auto;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  padding: 30px 0px;
}
.container .leftComponents{
  flex-basis: 40%;
}
.container .rightComponents{
  flex-basis: 50%;
}

/*When the form is off*/
 .container .formOff{
  flex-basis: 50%;
  margin: auto auto;
  display: flex;
  flex-direction: column;
  align-items: center;
} 

/* The Nothing Elements */
.nothing {
  margin: 25px auto ;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 400px;
}
.icons {
  width:70px;
}
.nothing p{
  font-size: 20px;
  padding: 15px 0px 10px 0px;
}

/* The Header Stlye */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header h1{
  padding-right: 15px;
}
 .date{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 20px;
}

/*the style on the date*/
 .date .dateBord{
  background-color: #000;
  color: #f1f1f1;
  padding: 20px;
  margin: 2px;
}

/*Todo style*/
.todo {
  background-color: #f1f1f1;
  color: #000;
  display: flex;
  flex-direction: column;
  margin: 15px 5px;
  padding: 10px;
  border-radius: 5px;
}
.todo h2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.todo p {
  font-weight: 600;
}
.todo p.description {
  font-weight: 200;
}
.completed {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

/*The note style */
.note{
  text-align: center;
}
/* Completed Component style */
.complet{
  margin-top: 35px;
  border-top: solid 5px green;
}
.complet p.note {
  text-align: center;
  padding-top: 10px;
}
/* the reminder class */
.reminder {
  border-top:2px solid lightseagreen ;
}

/* the Form styling */
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.form .section {
  display: flex;
  flex-direction: column;
}
.form .section input{
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
}
.form .section label {
  margin-bottom: 10px;
}
.form .remind {
  display: flex;
  flex-direction: row;
}


/* Media Query */

@media all and (max-width: 911px) {
  .container .formOff .header{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .container .formOff .header button{
    margin-top: 10px;
  }
  .container .formOff .header h1{
    font-size: 35px;
  }
}

@media all and (max-width: 865px ) {
  .container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
    margin: auto;
    padding: 30px;
  }
  .container .leftComponents{
    width: 100%;
  }
  .container .rightComponents{
    width: 100%;
  }
}

@media all and (max-height:300px){
  .container .rightComponents .header{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .container .rightComponents .header h1{
    font-size: 15px;
    text-align: center;
  }
  .completed {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    
  }
  .date .dateBord{
    padding: 15px;
    margin: 1px;
  }

}